import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '@/store'
const Laout = () => import('@/views/layout')
const ZongtiGaishu = () => import('@/views/zongtigaishu')
const JianceFenxi = () => import('@/views/jiancefenxi')
const User = () => import('@/views/user')
const UserLogin = () => import('@/views/denglu')

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: Laout,
    redirect: '/ZongtiGaishu',
    children: [
      { path: '/ZongtiGaishu', component: ZongtiGaishu, meta: { index: '/ZongtiGaishu' } },
      { path: '/JianceFenxi', component: JianceFenxi, meta: { index: '/JianceFenxi' } },
      { path: '/user', component: User, meta: { index: '/user' } }

    ]
  },
  {
    path: '/login',
    component: UserLogin
  }
]

const router = new VueRouter({
  routes
})

// 所有的路由在正真被访问之前(解析渲染对应组件页面)都会先经过守卫
// 全局前置守卫
// to：到哪去，到哪去的完整路由信息对象（路径，参数）
// from：从哪儿来，从哪儿来完整路由信息对象（路径，参数）
// next（）：是否放行
// 1.next（） 直接放行，去to里要去的地址
// 2.next（路径）拦截，去括号内指定的路径

// 定义一个数组用来存放需要权限的页面
const authUrls = ['/ZongtiGaishu', '/JianceFenxi', '/user']
router.beforeEach((to, from, next) => {
  // console.log(to, from, next)
  if (!authUrls.includes(to.path)) {
    // 非权限页面放行
    next()
    return
  }
  // 是权限页面 需要判断token
  // 可以把获取封装成一个函数
  // const token = store.state.user.userInfo.token
  const token = store.getters.getUserName
  if (token) {
    next()
  } else {
    next('/login')
  }
})

export default router
