import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    userName: ''
  },
  getters: {
    // 计算属性
    getUserName (state) {
      return state.userName
    }
  },
  mutations: {
    // 修改
    userNameUpdate (state, newValue) {
      state.userName = newValue
    }
  },
  actions: {
    // 异步
  },
  modules: {
  }
})
